import React,{useState} from "react"
import 'materialize-css'

function App() {
  const [taskName, setTask] = useState("")
  const [taskDescription, setDesc] = useState("")
  const [creator, setCreat] = useState("")
  const [duration, setDura] = useState("")
  const [list,setList] = useState([])




  return (
    <div className="App">
    <header  className="card" style={{backgroundColor:"black", marginTop:"0px"}}>
    // <div className="card" style={{backgroundColor:"black", marginTop:"0px"}}>
    <center><h1 style={{color:"white"}}>Board Infinity </h1></center>
    // </div>

    </header>
    <center>
    <div className="card" style={{width:"70%" ,padding:"20px"}}>
    <input placeholder="Task Name" id="first_name" type="text" class="validate" value={ taskName}onChange = {(e)=>{setTask(e.target.value)}} style={{width:"50%"}} /><br />
    <input placeholder="Description" id="first_name" type="text" class="validate" value={taskDescription} onChange = {(e)=>{setDesc(e.target.value)}} style={{width:"50%"}} /><br />
    <input placeholder="Creator" id="first_name" type="text" class="validate"  value={creator} onChange = {(e)=>{setCreat(e.target.value)}} style={{width:"50%"}} /><br />
    <input placeholder="Duration(minutes)" id="first_name" type="text" class="validate" value={duration} onChange = {(e)=>{setDura(e.target.value)}} style={{width:"50%"}} /><br />
    <button class="btn waves-effect waves-light" style={{marginRight:"10px"}} type="submit" name="action" onClick={
      ()=>{
        fetch('/add',{
          method : "post",
          headers:{
            "Content-type" : "application/json"
          },
          body:JSON.stringify(
            {
              taskName,
              taskDescription,
              creator,
              duration
            }
          )
        }).then(res=> res.json())

        setTask("")
        setDesc("")
        setDura("")
        setCreat("")

      }
    }
     >Submit

    </button>
    <button class="btn waves-effect waves-light" type="submit" name="action" onClick={
      async()=>{
        fetch('/list',{
        }).then(async res=>{
          let data = await res.json()
          // console.log(data.)
          setList(data)

          console.log(JSON.stringify(list));
        })

      }
    }
     >List

    </button>
        </div>
    </center>

    <br />
    <center>
    <table style={{width:"80%"}}>
    <thead>
          <tr>
              <th>Task Name</th>
              <th>Task Description</th>
              <th>Creator </th>
              <th>Duration</th>
              <th>Created At</th>

          </tr>
        </thead>
    {
      list.length !== 0?
      list.map(e=>{
        return (

        <tbody>
          <tr>
              <td>{e.taskName}</td>
              <td>{e.taskDescription}</td>
              <td>{e.creator}</td>
              <td>{e.duration}</td>
              <td>{e.createdAt}</td>


          </tr>
        </tbody>

        )
      }):null
    }
    </table>
    </center>
    </div>





  );
}

export default App;
